.main-swiper{
    box-sizing: border-box;
    position: relative;
    text-align: center;
    width: 100%;
    &_header{
      display: flex;
      padding-left: 15px;
      padding-bottom: 20px;
      h1{
        background: #FFFFFF;
        filter: drop-shadow(0px 0px 25px rgba(0, 0, 0, 0.2));
        border-radius: 8px;
        width: 166px;
        padding: 20px;
        font-style: normal;
        font-weight: bold;
        font-size: 24px;
        line-height: 22px;
        text-align: center;
        color: #000000;
      }
    }
    &_footer{
      display: flex;
      align-items: flex-end;
      justify-content: flex-end;
      padding-top: 30px;
      padding-right: 50px;
      button{
        background: #F89F46;
        box-shadow: 0 4px 9px rgba(248, 159, 70, 0.7);
        border-radius: 52px;
        border: none;
        cursor: pointer;
        width: 128px;
        height: 44px;
        outline: none;
        a{
          text-decoration: none;
          font-weight: bold;
          font-size: 24px;
          line-height: 29px;
          color: #FFFFFF;
        }
      }
    }
    .wrapper{
      width: 100%;
      box-sizing: border-box;
      display: flex;
      overflow-x: scroll;
      padding: 20px 0;
      position: relative!important;
    }
    ::-webkit-scrollbar-button{
      width: 40px;
    }
    ::-webkit-scrollbar-track {
      background: #FFFFFF !important;
      border: 3px solid #43A948 !important;
      border-radius: 15px;
    }
    ::-webkit-scrollbar {
      height: 17px;
    }
    ::-webkit-scrollbar-thumb {
      background: #43A948;
      border-radius: 15px;
      width: 100px !important;
    }
  
    @media screen and (max-width: 768px) {
      padding-top: 75px;
    }
    @media screen and (max-width: 450px) {
      position: relative;
      &_header{
        max-width: 147px;
        min-width: 121px;
        h1{
          padding: 12px 0;
          font-size: 18px;
          line-height: 22px;
        }
      }
      &_footer{
        position: absolute;
        top: 58px;
        right: -32px;
        button{
          a{
            font-size: 18px;
            line-height: 22px;
          }
        }
      }
    }
  }

.main-slider-container {
    border: 4px solid transparent;
    border-radius: 10px;
    margin-bottom: 40px;
    padding: 30px;
    cursor: pointer;
    transition: all ease 0.5s; 
    &:hover {
        border: 4px solid #3f51b5;
    }
}

.food-slider-list-goods {
  margin-top: 20px;
  background-color: #eee;
  list-style-type: none;
  border-radius: 5px;
  padding: 10px 0;
  & li {
    padding: 10px 5px; 
    height: 80px;
    border-bottom: 1px solid grey;
  }
  & div {
    display: flex;
    align-items: center;
    justify-content: space-between;
    & img {
      max-height: 80px;
      max-width: 80px;
    }
  }
}