.add-blog-img {
    border: 1px solid #aaa;
    margin: 0 20px;
    &:hover {
        border: 3px solid #aaa;
        cursor: pointer;
        border-radius: 2px;
    }
}

.add-blog-images {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
}