.action-slider-ul {
    width: 90%;
    list-style-type: none;
    border: 2px solid black;
    padding: 0;
    &_label-li {
        background-color: #eee;
        cursor: pointer;
        text-align: center;
        &:hover {
            background-color: rgb(209, 209, 209);
        }
        padding: 10px 0;
        border-bottom: 1px solid black;
        h5 {
            margin: 0;
        }
    }
    &_label-edit {
        background-color: lightblue;
        padding: 20px 0;
        text-align: center;
        display: flex;
        flex-direction: column;
        align-items: center;
        h3 {
            margin: 0;
        }

        textarea {
            resize: vertical;
            width: 500px;
            font-size: 20px;
            padding: 5px;
            height: 40px;
        }

        button {
            font-size: 18px;
            padding: 10px;
            margin-top: 20px;
        }

    }
    .label-closed {
        display: none;
    }
    .label-opened {
        display: flex;
    }
}