
.orders_list_container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: left;
    padding: 20px;
    width: 100%;
    margin-top: 20px;
    background: #FFFFFF;
    box-shadow: 0 0 25px rgba(0, 0, 0, 0.2);
}
.mainese{
    width:70%
}
.order_history_footer{
    display: flex;
    flex-direction: column;
    width: 100%;
    justify-content: center;
    align-items: center;
   
    & button {
      background: #F89F46;
      color: white;
      font-size: 18px;
      font-weight: 700;
      border: none;
      outline: none;
      height: 37px;
      border-radius: 30px;
      padding: 15px 22px;
      display: flex;
      align-items: center;
    }
    &_list{
      width: 100%;
      padding: 20px 0;
      .content{
        display: flex;
        flex-direction: column;
        align-items: center;
        background: #FFFFFF;
        box-shadow: 0 0 25px rgba(0, 0, 0, 0.2);
        border-radius: 8px;
        width: 100%;
        color: #3F3737;
        &_header, &_body, &_footer{
          display: flex;
          justify-content: space-between;
          width: 100%;
          padding: 10px 0;
          font-weight: normal;
          font-size: 18px;
          line-height: 22px;
        }
        &_header{
          border-bottom: 1px solid #C4C4C4;
          &_left{
            display: flex;
            align-items: baseline;
            padding-left: 38px;
            h2{
              font-weight: bold;
              font-size: 24px;
              line-height: 29px;
              padding-right: 18px;
            }
          }
          &_right{
            display: flex;
            align-items: center;
            padding-right: 38px;
            img{
              width: 46px;
              height: 46px;
              border-radius: 100%;
            }
            p{
              font-weight: bold;
              padding: 0 26px;
            }
            span{
              padding-left: 14px;
            }
          }
        }
        &_body{
          display: flex;
          flex-direction: column;
          &_block{
            display: flex;
            justify-content: space-between;
            align-items: center;
            padding: 0 38px;
            .right{
              display: flex;
              justify-content: space-between;
              span{
                padding-left: 68px;
                font-weight: bold;
              }
            }
          }
        }
        &_footer{
          justify-content: flex-end;
          align-items: baseline;
          h1, h2{
            padding-right: 38px;
            font-weight: bold;
            font-size: 24px;
            line-height: 29px;
          }
        }
      }
    }
  }