.create-order {
    display: flex;
    padding: 20px;
    padding-bottom: 40px;
    flex-direction: column;
    align-items: center;

    & h3 {
        align-self: center;
        margin-bottom: 40px;
    }

    & h4 {
        color: #aaa;
        align-self: center;
        margin: 10px 0;
    }

    &_container {
        display: flex;
        flex-direction: row;
        align-items: flex-start;
        justify-content: flex-start;
        margin: 10px 0;
        & p {
            margin: 0;
            margin-top: 3px;
        }
        input {
            color: #1a1a1a;
            border: 1px solid #d9d9d9;
            padding: 1px 10px;
            font-size: 13px;
            line-height: 28px;
            height: 30px;
            width: 300px;
            margin-left: 30px;
        }

        textarea {
            color: #1a1a1a;
            border: 1px solid #d9d9d9;
            padding: 4px 10px;
            font-size: 13px;
            line-height: 18px;
            height: 30px;
            width: 300px;
            margin-left: 30px;
        }
    }

    &_container__address-inputs {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        width: 450px;
        &_item {
            width: 50%;
            display: flex;
            flex-direction: row;
            align-items: center;
            padding: 10px;
            box-sizing: border-box;
            p {
                width: 40%;
                word-wrap: break-word;
            }
            input {
                width: 50%;
                height: 50%;
                color: #1a1a1a;
                border: 1px solid #d9d9d9;
                padding: 1px 10px;
                font-size: 13px;
            }
        }
    }

    &__omposition {
        width: 100%;
        display: flex;
        flex-direction: column;
        border-radius: 10px;
        border: 3px solid #000;
        &_header {
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            align-items: center;
            background-color: #eee;
            border-top-left-radius: 10px;
            border-top-right-radius: 10px;
            border-bottom: 1px solid black;
            padding: 5px 10px;
            padding-bottom: 10px;
            & h3 {
                margin: 0;
            }
        }
        &_footer {
            display: flex;
            flex-direction: column;
            padding: 5px 20px;
            background-color: #eee;
            align-items: flex-end;
            & p {
                margin: 5px 0;
            }
        }

        &_body {
            margin: 10px 0;
            border: 1px solid black;
            align-self: center;
            width: 98%;
            box-sizing: border-box;
        }

    }

}