.all-admins_header {
    width: 100%;
    height: 40px;
    background-color: snow;
    display: flex;
    align-items: center;
    justify-content: center;
    border-bottom: 1px solid #eee;

    &__item {
        color: #3f51b5;
        margin: 0 5px;
        &:hover {
            text-decoration: underline;
        }
        &-active {
            text-decoration: underline;
        }
    }
}