.list{
  display: flex;
  align-items: stretch;
  box-sizing: border-box;
  padding: 0 15px;
  margin-bottom: 30px;
  text-decoration: none;
  width: 400px;
  &_context{
    box-sizing: border-box;
    background: #FFFFFF;
    color: #3F3737;
    box-shadow: 0 0 25px rgba(0, 0, 0, 0.2);
    border-radius: 8px;
    width: 100%;
    img{
      max-width: 100%;
    }
    .footer{
      padding: 10px 20px 15px 45px;
      word-break: break-word;
      h1{
        font-weight: bold;
        font-size: 18px;
        line-height: 22px;
      }
      p{
        font-weight: normal;
        font-size: 12px;
        line-height: 159.11%;
      }
      li{
        list-style: none;
        padding: 8px 0;
        span{
          font-size: 18px;
          line-height: 22px;
          text-decoration-line: underline;
          color: #C4C4C4;
        }
      }
    }
  }
}

  @media screen and (max-width: 1910px) {
        .list {
          width: 370px;
        }
  }
  
  @media screen and (max-width: 1650px) {
        .list {
          width: 350px;
        }
  }
  
  @media screen and (max-width: 1060px) {
        .list {
          width: 320px;
        }
  }


  .blog-article_section{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-family: Century Gothic;
    font-style: normal;
    color: #000000;
    font-weight: normal;
    background: #FFFFFF;
    box-sizing: border-box;
    padding: 0 20px;
    .blog_container{
      max-width: 1146px;
      display: flex;
      flex-direction: column;
      background: #FFFFFF;
      box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.2);
      border-radius: 5px;
      .imageTitle {
        width: 100%;
        border-radius: 5px;
        overflow: hidden;
        img {
          max-width: 100%;
          border-radius: 5px;
        }
      }
      &_filling{
        display: flex;
        flex-direction: column;
        padding: 30px 20px 20px 50px;
        &_header{
          display: flex;
          justify-content: space-between;
          align-items: center;
          word-break: break-word;
          white-space: pre-line;
          h4{
            font-weight: bold;
            font-size: 18px;
            line-height: 22px;
            color: #3F3737;
          }
          p{
            font-size: 18px;
            line-height: 159.11%;
            color: #3F3737;
          }
        }
        &_center{
          word-break: break-word;
          white-space: pre-line;
          padding-bottom: 40px;
          span{
            font-size: 18px;
            line-height: 22px;
          }
        }
        &_bottom{
          display: flex;
          justify-content: space-between;
          align-items: center;
          amp-img{
            width: 155px !important;
            height: 21px !important;
          }
          p{
            font-weight: bold;
            font-size: 18px;
            line-height: 22px;
            color: #3F3737;
            &:hover {
              text-decoration: underline;
              cursor: pointer;
            }
          }
        }
      }
    }
  
    @media screen and (min-width: 1060px) and (max-width:1500px){
      .blog_container {
        &_filling{
          amp-img{
            height: 21px !important;
          }
        }
      }
    }
  
    @media screen and (max-width:992px){
      .blog_container {
        &_filling{
          amp-img{
            height: 21px !important;
          }
        }
      }
    }
    @media screen and (max-width: 768px) {
      .blog_container{
        &_filling {
          amp-img{
            height: 21px !important;
          }
          &_header {
            flex-direction: column;
            align-items: flex-start;
            h4{
              margin-bottom: 0;
            }
            p{
              padding-bottom: 30px;
            }
          }
        }
      }
    }
  
    @media screen and (max-width: 450px) {
      .blog_container {
        &_filling {
          padding: 20px;
        }
      }
    }
  }

