.masonry-layout__panel {
    break-inside: avoid;
    width: 380px;
    background: #FFFFFF;
    box-shadow: 0 0 25px rgba(0, 0, 0, 0.2);
    border-radius: 8px;
    word-wrap: break-word;
    margin-bottom: 40px;

    &__header {
      overflow: hidden;
      & img {
        width: 100%;
        object-fit: cover;
        object-position: 0 0;
        border-top-left-radius: 7px;
        border-top-right-radius: 7px;
      }
    }

    &__content {
      padding: 10px 40px;
      white-space: pre-line;
      & h4 {
        margin: 0;
        padding: 0;
        font-size: 18px;
        font-weight: 700;
        max-height: 70px;
        overflow: hidden;
      }
      & p {
        font-size: 12px;
        font-weight: 400;
        line-height: 1.7;
        max-height: 250px;
        overflow: hidden;
      }
    }
}

@media screen and ( max-width: 450px ){
    .masonry-layout__panel {
        width: 300px !important;
    }
  }

  @media screen and ( max-width: 350px ){
    .masonry-layout__panel {
        width: 250px !important;
    }
  }