.seo {
    width: 95%;
    border-radius: 10px;
    border: 3px solid #aaa;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 10px;
    margin: 20px 0;
    h5 {
        margin: 0;
        align-self: center;
    }
    .textarea-cont {
        display: flex;
        align-items: center;
        textarea {
            height: 30px;
            font-size: 18px;
            margin-left: 20px;
            width: 500px;
            resize: vertical;
            padding: 0 5px;
        }
        &_descr {
            height: 100px !important;
        }
    }
    .warning {
        color: brown;
        font-weight: bold;
    }
}