.orders-list__content-area {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    &__controls {
        display: flex;
        flex-direction: column;
        width: 100%;
        padding-bottom: 8px;
        border-bottom: 2px solid #eee;
        &__top {
            & h1 {
                margin: 0;
            }
            & button {
                height: 35px;
            }
            padding: 10px 20px;
            display: flex;
            justify-content: space-between;
            align-items: center;
        }

        &__search {
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            padding-right: 20px;
            align-items: center;
            &__inputs {
                display: flex;
                flex-direction: row;
                margin-top: 15px;
                &__item {
                    display: flex;
                    flex-direction: column;
                    width: 200px;
                    margin: 0 20px;
                    & label {
                        color: grey;
                    }
                }
            }
        }

    }
}