.food-block-container {
    width: 122px;
    height: 126px;
    box-sizing: border-box;
    .list_header{
      display: flex;
      justify-content: center;
      align-items: center;
      box-sizing: border-box;
      padding-top: 15px;
      width: 100%;
      height: 90px;
      overflow: hidden;
      img{
        width: auto;
        max-width: 80%;
        height: 90%;
      }
    }
}