.add-good-in-order {
    width: 90%;
    height: 90%;
    background-color: white;
    position: fixed;
    top: 5%;
    left: 5%;
    z-index: 100;
    border-radius: 10px;
    border: 2px solid black;
    display: flex;
    flex-direction: row;
    &__left {
        width: 20%;
        height: 100%;
        background-color: #eee;
        border-bottom-left-radius: 10px;
        border-top-left-radius: 10px;
        padding: 10px;
        box-sizing: border-box;
        display: flex;
        flex-direction: column;
        & p {
            margin: 5px 0;
            font-size: 13px;
        }
        & select {
            width: 100px;
            font-size: 13px;
        }
        & button {
            margin-top: 20px;
        }
    }
    &__right {
        width: 80%;
        height: 100%;
        display: flex;
        box-sizing: border-box;
        flex-direction: column;
        align-items: center;
        overflow-y: scroll;
        &_header {
            width: 100%;
            background-color: rgb(250, 248, 248);
            padding: 18px 20px;
            box-sizing: border-box;
            border-top-right-radius: 10px;
            display: flex;
            justify-content: space-between;
            & h3 {
                margin: 0;
            }
        }

        & ul {
            width: 95%;
            padding: 0;
            list-style-type: none;
            border: 1px solid black;
            padding: 10px 0;
            & li {
                width: 100%;
                border-bottom: 1px solid #aaa;
                display: flex;
                flex-direction: row;
                padding: 10px 0;
            }
        }
    }
}

.ul-li-good {
    width: 45%;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
}

.ul-li-price {
    width: 20%;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
}

.ul-li-quantity {
    width: 15%;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
}

.ul-li-add {
    width: 20%;
    display: flex;
    justify-content: center;
    align-items: center;
}

