.editor-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    & ul {
        list-style-type: none;
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-top: 50px;
        & li {
            font-size: 24px;
            font-weight: 700;
            margin-bottom: 20px;
            &:hover {
                text-decoration: underline;
            }
        }
    }
}